  <div class="main-wrapper">
    <section class="blog-list px-3 py-5 p-md-5">
      <div class="container">
        <div v-for="item in $store.state.daily_list.slice(currentPage*20, (currentPage+1)*20)" v-bind:key="item.title" class="item mb-5">
          <div class="media">
            <img
              class="mr-3 img-fluid post-thumb d-none d-md-flex"
              :src="'/daily/images/'+ item.time_str.replaceAll('-', '')+'/1.png'"
              onerror="this.src='../assets/logo.png'"
            />
            <div class="media-body">
              <h3 class="title mb-1">
                <a :href="'/daily/html/'+item.url" target="_blank">{{item.title}}</a>
              </h3>
              <div class="meta mb-1">
                <span class="date">{{item.time_str}}</span
                ><span class="time">{{item.read_time}}</span
                ><span class="comment"><a href="#">{{item.comments_num}}</a></span>
              </div>
              <div class="intro intro2">
                {{item.desc}}
              </div>
              <a class="more-link" href="blog-post.html">Read more &rarr;</a>
            </div>
            <!--//media-body-->
