		  <a :class="{'nav-link-next':true, 'nav-item':true, 'nav-link':true, 'rounded-right':true, 'd-none':next_page_active}" @click="currentPage = currentPage + 1;">Next<i class="arrow-next fas fa-long-arrow-alt-right"></i></a>
