export default {
  name: 'Home',
  components: {
    // HelloWorld
  },
  data() {
    return {
        currentPage: 0,
    }
  },
  computed:{
    next_page_active(){
      return (this.currentPage+1) * 20 >= this.$store.state.daily_list.length
    },
    previous_page_active(){
      return this.currentPage == 0;
    }
  }
}

