<template>
  <profile></profile>
  <router-view></router-view>
</template>

<script>
import Profile from "./components/Profile";
import Home from "./views/Home";
import Daily from "./views/Daily";
import Archive from "./views/Archive";
import axios from 'axios';
import store from "./store"

export default {
  data() {
    return {};
  },
  components: {
    Profile,
    Home,
    Archive,
  },

  store: store,

  created(){
    axios
      .get("/post_meta.txt")
      .then(response => {
        this.$store.commit("InitPostData", response.data);
      }).catch(error => {
        console.log(error);
      });
    
    axios
      .get("/daily_meta.txt")
      .then(response => {
        this.$store.commit("InitDailyData", response.data);
      }).catch(error => {
        console.log(error);
      });
    

  },
};
</script>

