<template>
  <div class="main-wrapper">
    <section class="cta-section theme-bg-light py-5">
      <div class="container text-center">
        <h2 class="heading">Tonggege's Blog</h2>
        <div class="intro">
          Welcome to my blog. Search for the blog by key words
        </div>
        <div class="signup-form form-inline justify-content-center pt-3">
          <div class="form-group">
            <label class="sr-only" for="semail">Your email</label>
            <input
              class="form-control mr-md-1 semail"
              v-model="search_key"
              placeholder="..."
              @keyup.enter="search_key_submit"
            />
          </div>
          <button @click="search_key_submit" class="btn btn-primary"></button>
        </div>
      </div>
      <!--//container-->
    </section>

    <section class="blog-list px-3 py-5 p-md-5">
      <div class="container">
        <div
          v-for="item in this.show_list.slice(
            currentPage * 10,
            currentPage * 10 + 10
          )"
          v-bind:key="item.title"
          class="item mb-5"
        >
          <div class="media">
            <img
              class="mr-3 img-fluid post-thumb d-none d-md-flex"
              :src="'/post/images/'+ item.time_str.replaceAll('-', '')+'/1.png'"
              onerror="this.src='../assets/logo.png'"
            />
            <div class="media-body">
              <h3 class="title mb-1">
                <a :href="'/post/html/'+item.url" target="_blank">{{ item.title }}</a>
              </h3>
              <div class="meta mb-1">
                <span class="date">{{ item.time_str }}</span>
                <span class="time">{{ item.read_time }}</span>
                <span class="comment"
                  ><a href="#">{{ item.comments_num }}</a></span
                >
              </div>
              <div class="intro intro2">{{ item.desc }}</div>
              <a class="more-link" :href="item.url">Read more &rarr;</a>
            </div>
            <!--//media-body-->
          </div>
          <!--//media-->
        </div>

        <nav class="blog-nav nav nav-justified my-5">
          <a :class="{'nav-link-prev':true, 'nav-item':true, 'nav-link':true, 'rounded-left':true,  'd-none':previous_page_active}" @click="currentPage = currentPage - 1;">Previous<i class="arrow-prev fas fa-long-arrow-alt-left"></i></a>
				  <a :class="{'nav-link-next':true, 'nav-item':true, 'nav-link':true, 'rounded-right':true, 'd-none':next_page_active}" @click="currentPage = currentPage + 1;">Next<i class="arrow-next fas fa-long-arrow-alt-right"></i></a>
        </nav>
      </div>
    </section>

    <footer class="footer text-center py-2 theme-bg-dark">
      <!--/* This template is released under the Creative Commons Attribution 3.0 License. Please keep the attribution link below when using for your own project. Thank you for your support. :) If you'd like to use the template without the attribution, you can buy the commercial license via our website: themes.3rdwavemedia.com */-->
      <small class="copyright"
        >Designed with <i class="fas fa-heart" style="color: #fb866a"></i> by
        <a href="http://themes.3rdwavemedia.com" target="_blank"
          >Xiaoying Riley</a
        >
        for developers</small
      >
    </footer>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import logo from "../assets/logo.png";

export default {
  name: "Home",
  data() {
    return {
      currentPage: 0,
      search_key: "",
      show_list: [],
      logo,
    };
  },
  computed: {
    next_page_active(){
      return (this.currentPage+1) * 10 >= this.show_list.length
    },
    previous_page_active(){
      return this.currentPage == 0;
    }
  },
  components: {
    // HelloWorld
  },
  methods:{
    search_key_submit() {
      let key_list = this.search_key.split(" ").filter((v, i) => {
        return v != ""
      }).map((v, i) => {
        return v.trim()
      });

      let _show_list = []
      _show_list = _show_list.concat(this.$store.state.post_list);

      this.show_list = _show_list.filter((v) => {
        for (let word of key_list){
          if (v.title.indexOf(word) != -1){
            return true
          }
        }
        return false
      }).sort((b, a) => {
        let a_time_str = a.time_str.split("-")
        let b_time_str = b.time_str.split("-")
        if (parseInt(a_time_str[0]) > parseInt(b_time_str[0])) {
          return 1
        }
        if (parseInt(a_time_str[0]) < parseInt(b_time_str[0])) {
          return -1
        }
        
        let a_month = 0
        let b_month = 0
        a_month = a_time_str[1].slice(0, 1) == '0'?a_time_str[1].slice(1):a_time_str[1]
        b_month = b_time_str[1].slice(0, 1) == '0'?b_time_str[1].slice(1):b_time_str[1]
        if (parseInt(a_month) > parseInt(b_month)) {
          return 1
        }
        if (parseInt(a_month) < parseInt(b_month)) {
          return -1
        }

        let a_day = 0
        let b_day = 0
        a_day = a_time_str[2].slice(0, 1) == '0'?a_time_str[2].slice(1):a_time_str[2]
        b_day = b_time_str[2].slice(0, 1) == '0'?b_time_str[2].slice(1):b_time_str[2]
        
        if (parseInt(a_day) > parseInt(b_day)) {
          return 1
        }
        if (parseInt(a_day) < parseInt(b_day)) {
          return -1
        }
        return 0
      });



    }
  },
  mounted(){
    this.show_list = this.$store.state.post_list;
  },
};
</script>
