import { createStore } from 'vuex'

export default createStore({
  state: {
    post_list: [],
    tags_table: {},
    date_table: [],
    daily_list: [],
  },
  getters: {
    tag_list(state){
      return Object.keys(state.tags_table)
    }
  },
  mutations: {
    InitPostData(state, rawData) {
      rawData = rawData.trim()
      const dataArray = rawData.split("\n");
      for (let entryline of dataArray) {
        let entryArr = entryline.split(",");
        let postEntry = {
          url: entryArr[0],
          img_url: "http://www.baidu.com/img/PCtm_d9c8750bed0b3c7d089fa7d55720d6cf.png",
          title: entryArr[1],
          time_str: entryArr[2],
          tags: entryArr[3].split(" "),
          desc: entryArr[4]
        }
        state.post_list.push(postEntry);
      }

      state.post_list.sort((b, a) => {
        let a_time_str = a.time_str.split("-")
        let b_time_str = b.time_str.split("-")
        if (parseInt(a_time_str[0]) > parseInt(b_time_str[0])) {
          return 1
        }
        if (parseInt(a_time_str[0]) < parseInt(b_time_str[0])) {
          return -1
        }
        
        let a_month = 0
        let b_month = 0
        a_month = a_time_str[1].slice(0, 1) == '0'?a_time_str[1].slice(1):a_time_str[1]
        b_month = b_time_str[1].slice(0, 1) == '0'?b_time_str[1].slice(1):b_time_str[1]
        if (parseInt(a_month) > parseInt(b_month)) {
          return 1
        }
        if (parseInt(a_month) < parseInt(b_month)) {
          return -1
        }

        let a_day = 0
        let b_day = 0
        a_day = a_time_str[2].slice(0, 1) == '0'?a_time_str[2].slice(1):a_time_str[2]
        b_day = b_time_str[2].slice(0, 1) == '0'?b_time_str[2].slice(1):b_time_str[2]
        
        if (parseInt(a_day) > parseInt(b_day)) {
          return 1
        }
        if (parseInt(a_day) < parseInt(b_day)) {
          return -1
        }
        return 0
      })

      for(let idx in state.post_list){
        for(let tag of state.post_list[idx].tags) {
          if (Object.keys(state.tags_table).indexOf(tag) != -1){
            state.tags_table[tag].push(parseInt(idx))
          }
          else {
            state.tags_table[tag] = [parseInt(idx)]
          }
        }
      }
    },

    InitDailyData(state, rawData) {
      rawData = rawData.trim()
      const dataArray = rawData.split("\n");
      for (let entryline of dataArray) {
        let entryArr = entryline.split(",");
        let postEntry = {
          url: entryArr[0],
          img_url: "http://www.baidu.com/img/PCtm_d9c8750bed0b3c7d089fa7d55720d6cf.png",
          title: entryArr[1],
          time_str: entryArr[2],
          tags: entryArr[3].split(" "),
          desc: entryArr[4]
        }
        state.daily_list.push(postEntry);
      }

      state.daily_list.sort((b, a) => {
        let a_time_str = a.time_str.split("-")
        let b_time_str = b.time_str.split("-")
        if (parseInt(a_time_str[0]) > parseInt(b_time_str[0])) {
          return 1
        }
        if (parseInt(a_time_str[0]) < parseInt(b_time_str[0])) {
          return -1
        }
        
        let a_month = 0
        let b_month = 0
        a_month = a_time_str[1].slice(0, 1) == '0'?a_time_str[1].slice(1):a_time_str[1]
        b_month = b_time_str[1].slice(0, 1) == '0'?b_time_str[1].slice(1):b_time_str[1]
        if (parseInt(a_month) > parseInt(b_month)) {
          return 1
        }
        if (parseInt(a_month) < parseInt(b_month)) {
          return -1
        }

        let a_day = 0
        let b_day = 0
        a_day = a_time_str[2].slice(0, 1) == '0'?a_time_str[2].slice(1):a_time_str[2]
        b_day = b_time_str[2].slice(0, 1) == '0'?b_time_str[2].slice(1):b_time_str[2]
        
        if (parseInt(a_day) > parseInt(b_day)) {
          return 1
        }
        if (parseInt(a_day) < parseInt(b_day)) {
          return -1
        }
        return 0
      })
    }
  }
})



